/**
 * @param {Number} timeStamp 
 * @param {Number} currentTime 
 * @returns {Boolean} 
 */
/**
 * 
 */
const isEarly = (timeStamp, currentTime) => {
    return timeStamp <= currentTime;
};

/**
 * @param {Number} num 
 * @returns {String} 
 * @description 1010
 */
const fillZero = num => {
    return num < 10 ? '0' + num : num;
};

/**
 * @param {Number} timeStamp 
 * @param {Number} startType 'year'
 */
const getDate = (timeStamp, startType) => {
    const d = new Date(timeStamp * 1000);
    const year = d.getFullYear();
    const month = fillZero(d.getMonth() + 1);
    const date = fillZero(d.getDate());
    const hours = fillZero(d.getHours());
    const minutes = fillZero(d.getMinutes());
    const second = fillZero(d.getSeconds());
    let resStr = '';
    if (startType === 'year') resStr = year + '-' + month + '-' + date + ' ' + hours + ':' + minutes + ':' + second;
    else resStr = month + '-' + date + ' ' + hours + ':' + minutes;
    return resStr;
};

/**
 * @param {String|Number} timeStamp 
 * @returns {String} 
 */
export const getRelativeTime = timeStamp => {
    const currentTime = (new Date()).getTime();

    // 
    const IS_EARLY = isEarly(timeStamp, currentTime);
    // 
    let diff = currentTime - timeStamp;
    // IS_EARLYfalse
    if (!IS_EARLY) diff = -diff;
    let resStr = '';
    const dirStr = IS_EARLY ? '' : '';

    if (diff < 1000) resStr = '';
    // 59
    else if (diff < 60000) resStr = parseInt(diff / 1000) + '' + dirStr;
    // 595959
    else if (diff >= 60000 && diff < 3600000) resStr = Math.floor(diff / 60000) + '' + dirStr;
    // 5959235959
    else if (diff >= 3600000 && diff < 86400000) resStr = Math.floor(diff / 3600000) + '' + dirStr;
    // 235959295959
    else if (diff >= 86400000 && diff < 2623860000) resStr = Math.floor(diff / 86400000) + '' + dirStr;
    // 295959364235959
    else if (diff >= 2623860000 && diff <= 31567860000 && IS_EARLY) resStr = getDate(timeStamp);
    else resStr = getDate(timeStamp, 'year');
    return resStr;
};

export default function (time) {
    return getRelativeTime(time);
}